.whellLeft {
  position: relative;
}
.btnWhellStart {
  height: 90px;
  width: 90px;
  line-height: 90px;
  color: white;
  text-align: center;
  background-color: #e13d3b;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  cursor: pointer;
  /* box-shadow: 0 3px 0 #e13d3b, 0 5px 15px -4px #000; */
  box-shadow: 0 5px 15px -4px #000;
  border: 0;
  transition: box-shadow 0.2s ease-in-out, margin-top 0.2s ease-in-out;
  margin-top: 1px;
}

.button-jittery {
  animation: jittery 3s infinite;
}

.button-jittery:hover {
  box-shadow: none;
  animation: heartbeat 1s infinite;
}
.button-jittery:focus,
.button-jittery:active {
  animation: none;
}

@media screen and (max-width: 576px) {
  .btnWhellStart {
    height: 65px;
    width: 65px;
    line-height: 65px;
  }
}

@keyframes jittery {
  5%,
  50% {
    transform: translate(-50%, -50%) scale(1);
  }
  10% {
    transform: translate(-50%, -50%) scale(0.9);
  }
  15% {
    transform: translate(-50%, -50%) scale(1.1);
  }
  20% {
    transform: translate(-50%, -50%) scale(1.1) rotate(-5deg);
  }
  25% {
    transform: translate(-50%, -50%) scale(1.1) rotate(5deg);
  }
  30% {
    transform: translate(-50%, -50%) scale(1.1) rotate(-3deg);
  }
  35% {
    transform: translate(-50%, -50%) scale(1.1) rotate(2deg);
  }
  40% {
    transform: translate(-50%, -50%) scale(1.1) rotate(0);
  }
}
.ant-modal-body {
  padding: 10px;
}
@keyframes heartbeat {
  50% {
    transform: translate(-50%, -50%) scale(1.1);
  }
}
